//
// progress.scss
//

.progress {
    box-shadow: 0px 1px 2px 1px rgba(var(--#{$prefix}dark-rgb), .1) inset;
}
// Custom height

.progress-xs {
    height: 1px;
}

// Progress height small
.progress-sm {
    height: 5px;
}

// Progress height medium
.progress-md {
    height: 8px;
}

// Progress height large
.progress-lg {
    height: 12px;
}

// Progress height extra large
.progress-xl {
    height: 15px;
}